@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap');

.App {
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  background-color: #000;
  height: 100vh;
  padding-top: 10rem;
}

/* ******************Todo List****************** */
.container {
  background-image: repeating-linear-gradient(#893af0, #000);
  min-width: 300px;
  width: fit-content;
  border: 1px solid #893af0;
  border-radius: 2rem;
  box-shadow: 0px 0px 35px 5px #893af0;
}
.input_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 3rem;
}
.input {
  width: 50vw;
  border-radius: 0.8rem;
  border: none;
  box-shadow: 0px 0px 35px 2px #000000;
  color: rgba(0, 0, 151, 0.568);
}
.buttons {
  display: flex;
  gap: 1rem;
}

.list_component {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
  margin-bottom: 1.5rem;
}
.list_component h2 {
  width: 500px;
  font-size: 18px;
  font-weight: 700;
  text-align: left;
  padding: 5px 0 5px 10px;
  border-radius: 1rem;
  margin-top: 0.5rem;
}
.btn {
  border-radius: 0.8rem;
}
.btn:is(:hover, :focus) {
  box-shadow: 0px 0px 15px 2px #893af0;
}
h1 {
  font-weight: 700;
  color: #36175e5b;
  margin-top: 1rem;
  margin-bottom: 3rem;
}
